export default {
	data() {
		return {
			canSee: false,
			// mobile: '15915407513',
			mobile: '',
			code: '',
			state: false, //是否开启倒计时,
			time: 70, //总时间，单位秒
			getCodeTxt: '获取验证码'
		}
	},
	onLoad() {

	},
	methods: {
		changeSee() {
			this.canSee = !this.canSee
		},
		toLogin() {
			uni.redirectTo({
				url: "./login"
			})
		},
		//点击获取验证码
		getCode() {
			if (this.mobile == '') {
				this.showToast('请输入手机号码')
				return
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/UserCenter/sendBindActiveCode',
				data: {
					token: uni.getStorageSync('token'),
					phone: this.mobile,
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.showToast('验证码已发送')
						//开始倒计时
						this.state = true;
						//执行倒计时
						this.checkingTime();
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//倒计时
		checkingTime() {
			var _this = this
			var timer = setInterval(function() {
				_this.time--
				if (_this.time <= 0) {
					clearInterval(timer)
					_this.state = false;
					_this.getCodeTxt = '重新获取'
					_this.time = 70
				}
			}, 1000)
		},
		//绑定手机号
		bindMobile() {
			if (this.mobile == '') {
				this.showToast('输入手机号不能为空')
				return
			} else if (this.code == '') {
				this.showToast('验证码不能为空')
				return
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/bindUserPnone",
				data: {
					token: uni.getStorageSync('token'),
					phone: this.mobile,
					active_code: this.code,
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.navigateBack({
							delta: 1,
							success: () => {
								uni.showToast({
									title: '绑定成功',
									icon: 'success'
								})
							}
						})
						uni.setStorageSync('mobile',this.mobile)
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	}
}
